import React from "react";

import Navbar from '../components/navbar';
import Header from '../components/header';
import Modal from '../components/modalWindow';
import ModalSignIn from '../components/modalWindowSignIn';



class PageClass extends React.Component {
    state = {
        ModalActive : false,
        auth : false,
        bech32 : null,
        text : "Войти в Zeeves",
        picture : false,
        loading : false,
        ModalSignInActive : false,
        url : null
    }
    
    render () {
        var account = null;
        const {Long, bytes, units} = require('@zilliqa-js/util');
  
        const ModalSetActive = (value) => {
            this.setState({ModalActive : value})
        }
        const ModalSetActive2 = (value) => {
            this.setState({ModalSignInActive : value})
        }
        const SetLoading = (value) => {
            this.setState({loading : value});
        };
        const LogOug = () => {
            this.setState({auth : false , bech32 : null , text : "Войти в Zeeves"});
        };
        const walletInfo = async () => { 
            account = await window.Zeeves.auth();
            this.setState({auth : true});
            this.setState({text : account.bech32});
            this.setState({bech32 : account.bech32});
            ModalSetActive2(false);
            ModalSetActive(true);
          }
        const generatePic = async (value) => {
            await this.setState({ picture : false , url : null , loading : true })
            await this.setState({ loading : true ,  picture : true , url : " http://e754-34-140-227-165.ngrok.io/text2image/?message="+value})
            window.url = this.state.url;
        }

        const CHAIN_ID = 1;
        const MSG_VERSION = 1;
        const VERSION = bytes.pack(CHAIN_ID, MSG_VERSION);

        async function NFT () {

            const zilliqa = window.Zeeves;

            const walletInfo = await zilliqa.getSession();
            const address = walletInfo.byte20; // byte20 - adress in format of zil...
            console.log("Your account address is:");
            console.log(`${address}`);
            const myGasPrice = units.toQa('2000', units.Units.Li);
            var code = '';

            let response = await fetch("https://raw.githubusercontent.com/Zilliqa/ZRC/master/reference/nonfungible-token.scilla" 
            , {method : "GET"});
            if(response.ok){
                code = await response.text();
            }
            console.log("contract code is: ");
            console.log(code);
            const init = [
                    // this parameter is mandatory for all init arrays
                    {
                        vname: "_scilla_version",
                        type: "Uint32",
                        value: "0"
                    },
                    {
                        vname: "contract_owner",
                        type: "ByStr20",
                        value: `${address}`
                    },
                    {
                        vname: "name",
                        type: "String",
                        value: `Zeeves x NGS AINFT`
                    },
                    {
                        vname: "symbol",
                        type: "String",
                        value: `AINFT`
                    }
                ];
                console.log("init json is: ");
                console.log(JSON.stringify(init));
                const contract = zilliqa.contracts.new(code, init);
                try {
                    const [deployTx, nft] = await contract.deployWithoutConfirm({
                        version: VERSION,
                        gasPrice: myGasPrice,
                        gasLimit: Long.fromNumber(19244)
                    }, false);
            
                    if (nft.error) {
                        console.error(nft.error);
                        return;
                    }
            
                    console.log('TXN is:');
                    console.log(JSON.stringify(deployTx));		
            
                    console.log("\n\n\nNFT is:");
                    console.log(JSON.stringify(nft));
                    
            
                    // Полученный transaction id можно ввести на viewblock.io в виде:
                    // https://viewblock.io/zilliqa/tx/0xd371a915e4c07dc3855c4ca7a56b92c1d776ce59a25ecd37bf44f6adf7f31859
                    // Там изначально будет отображаться статус Dispatched ....
                    console.log(`The transaction id is:`, deployTx.id);
                    console.log(`Waiting transaction be confirmed`);
            
                    // Здесь код повиснет вплоть до подтверждения транзакции, которое может занимать до 1 минуты
                    const confirmedTxn = await deployTx.confirm(deployTx.id);
            
                    // Introspect the state of the underlying transaction
                    console.log(`Deployment Transaction ID: ${deployTx.id}`);
            
                    // Get the deployed contract address
                    console.log("The contract address is:");
                    console.log(nft.address);
                    NFT2(nft);
                } catch (e) {
                    console.error(e);
                }
        };

        async function NFT2(nft) {
        const {BN, Long, bytes, units} = require('@zilliqa-js/util');
        const zilliqa = window.Zeeves;

        // Если пользователь не авторизован - здесь откроется дополнительное окно
        // Если авторизован - то сразу вернется информация об адресе
        const walletInfo = await zilliqa.getSession();
        const address = walletInfo.byte20; // bech32 - adress in format of zil...
        console.log("Your account address is:");
        console.log(`${address}`);
        const myGasPrice = units.toQa('2000', units.Units.Li); // Gas Price that will be used by all transactions
        
        const nftAddr = nft.address; // сюда подставляете адрес контракта, который создался в ходе деплоя
    try {
        const contract = zilliqa.contracts.at(nftAddr);

        const callTx = await contract.call (
            'Mint',
            [
                {
                    vname: 'to',
                    type: 'ByStr20',
                    value: `${address}`,
                },
                {
                    vname: 'token_uri',
                    type: 'String',
                    value: window.url,
                }
            ],
            {
                version: VERSION,
                amount: new BN(0),
                gasPrice: myGasPrice,
                gasLimit: Long.fromNumber(10000),
            }
        );

        // check the pending status
        // const pendingStatus = await zilliqa.blockchain.getPendingTxn(callTx.id);
        // console.log(`Pending status is: `);
        // console.log(pendingStatus.result);

        // process confirm
        console.log(`The transaction id is:`, callTx.id);
        console.log(`Waiting transaction be confirmed`);
        //const confirmedTxn = await callTx.confirm(callTx.id);

        //console.log(`The transaction status is:`);
        //console.log(confirmedTxn.receipt);
    } catch (err) {
        console.log(err);
    }


}
         
        
        return (
            <div className="mt-5">
                <Navbar ModalSetActive2={ModalSetActive2} ModalSetActive={ModalSetActive} txt={this.state.text} auth={this.state.auth}/>
                <Header  loading={this.state.loading} SetLoading={SetLoading}  NFT={NFT} url={this.state.url}  generatePic={generatePic} picture={this.state.picture}/>
                <ModalSignIn walletInfo={walletInfo}  ModalSetActive={ModalSetActive2} ModalActive={this.state.ModalSignInActive}/>
                <Modal LogOug={LogOug} ModalSetActive={ModalSetActive} ModalActive={this.state.ModalActive} data={this.state.auth ? this.state.bech32 : null}/>
            </div>
            )
    }
}

export default PageClass;