import React from "react";


class ZeveesButtonClass extends React.Component {
    render(){
        
        return <div className="btn  myop"  onClick={() => { this.props.auth ? this.props.ModalSetActive(true) :  this.props.ModalSetActive2(true)  }} >{this.props.txt.length > 15 ? this.props.txt.substring(0 , 15)+"..." : this.props.txt  }</div>
    }
}



export default ZeveesButtonClass;