import React from 'react';
import logo from '../zeevesLogo.png';

class ModalWindow extends React.Component{
    state = {
        textToCopy : this.props.data
    }

    render () {
        return (
            <div className={this.props.ModalActive ? "modalWindow active col-12":"modalWindow"} onClick={() => {this.props.ModalSetActive(false)}}>
                <div className = "modalContente"  onClick={e => e.stopPropagation()}> 
                    <div className="modal-header">
                    Аккаунт подключен
                    <button type="button" className="btn-close" onClick={() => {this.props.ModalSetActive(false)}} data-bs-dismiss="alert" aria-label="Close"></button>
                    </div>
                    <div className="modal-body">
                        <img className="rounded mx-auto d-block "  src={logo} height="75" alt="logo" /><p className="text-center">Вы подключили аккаунт Zeeves</p>
                    </div>
                    <div  className="btn btn-outline-dark col-12 text-truncate my-2" onClick={() => {navigator.clipboard.writeText(this.props.data)}} > {this.props.data ? this.props.data : "zil228322"} </div>
                    
                        <div  className="btn my-3 d-block btn-outline-primary" onClick={() => {navigator.clipboard.writeText(this.props.data)}} >Скопировать</div>
                    
                    
                    <div className="modal-footer mt-5">
                        <button type="button" className="btn btn-danger col-12 " onClick={() => {this.props.LogOug(); this.props.ModalSetActive(false)}} >Выйти</button>
                    </div>
                    
                </div>
            </div>
        );
    }
}

export default ModalWindow;