import React from 'react';
import ZeevesButton from './zeeves_button';

class navBarClass extends React.Component {
  
  render(){
    
    return (<nav className="navbar navbar-expand-lg fixed-top setbg">
    <div className=" logo" href="#">VenchasS App</div>
    <div className="ms-auto">
        <ZeevesButton ModalSetActive2={this.props.ModalSetActive2} txt ={this.props.txt} ModalSetActive={this.props.ModalSetActive} auth={this.props.auth}/>
    </div>
 </nav>)
  }
}

export default navBarClass;