import React from "react";


class ImageClass extends React.Component {

    state = {
        imageStatus : false
    }
    handleImageLoaded() {
        this.setState({ imageStatus: true });
      }
    
    render () {
            
            return(
                <div>
                    {/* {this.state.imageStatus ? null : <div className="mx-auto"><div className="spinner-border mx-auto spin" role="status"><span className="visually-hidden">Загрузка...</span></div></div>} */}
                    <img width="70%" height="70%" src={this.props.url} onLoad={ () => {this.props.SetLoading(false)}} />
                </div>                   
            )
        
        
        
        

    }
}

export default ImageClass;