import React from 'react';
import ReactDOM from 'react-dom';
import Pic from '../2d2ff9a5b3934dab91273eb277df9be2_00000.jpg'
import Image from './image';



class HeaderClass extends React.Component {
    constructor(props) {
        super(props);
        this.state = {value: ''};
    
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      }
    
      handleChange(event) {
        this.setState({value: event.target.value});
      }
    
      handleSubmit(event) {
        alert('Отправленное имя: ' + this.state.value);
        event.preventDefault();
      }

    render () {
        return (
            <div className="pic"> 
                <div>
                    <div className="container-fluid min-vh-100">
                        <div className="row min-vh-100">
                            <div className="mt-2 col-xl-6 align-self-center text-center styled" >
                                Создание NFT на основe искуственного интеллекта

                                <input  value={this.state.value}  onChange={this.handleChange}  className="mt-5 mx-auto form-control col-8 text-center " type="text" placeholder="Введите текст"/>
                                    <button type="button" className="btn  btn-primary my-3 btn-lg  " onClick={() => { this.props.generatePic(this.state.value) }}>Сгенерировать картинку</button>
                                    <button type="button" className="btn btn-success ms-3  btn-lg " onClick={this.props.NFT}  >Сохранить в NFT</button>



                            </div>
                            <div className="col-xl-6 align-self-center text-center">
                                
                                    
                                
                                <div className="mt-3">
                                    {this.props.loading ?  <div className="mx-auto"><div className="spinner-border mx-auto spin" role="status"><span className="visually-hidden">Загрузка...</span></div></div> : null  }
                                    {this.props.picture ?  <Image loading={this.props.loading} SetLoading={this.props.SetLoading} picture={this.props.picture} url={this.props.url}/> : null}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            )
    }
}


export default HeaderClass;